import { useState, useEffect } from "react";
import { Calendar } from "react-calendar";
import moment from "moment";
import { useDispatch } from "react-redux";
import { dateDiff } from "../Reducer/DateDiffReducer";
import "../../node_modules/react-calendar/dist/Calendar.css";

function TravelCalendar() {
  const [selectedDates, setSelectedDates] = useState([]);
  const dispatch = useDispatch();

  const dayDiff = () => {
    if (selectedDates.length === 2) {
      const start = moment(selectedDates[0]);
      const end = moment(selectedDates[1]);
      const diff = end.diff(start, "days");

      if (diff === 0) {
        return ` .`;
      } else return `${diff} ${diff + 1} .`;
    } else {
      return "Please select two dates";
    }
  };

  useEffect(() => {
    if (selectedDates.length === 2) {
      const start = moment(selectedDates[0]);
      const end = moment(selectedDates[1]);
      const diff = end.diff(start, "days");
      dispatch(dateDiff(diff + 1));
    }
  }, [selectedDates]);

  const handleSelect = (momentRange) => {
    const newSelectedDates = [moment(momentRange[0]), moment(momentRange[1])];
    setSelectedDates(newSelectedDates);
  };

  return (
    <div>
      <Calendar
        value={selectedDates}
        onChange={handleSelect}
        formatDay={(locale, date) =>
          date.toLocaleDateString("ko", { day: "numeric" })
        } // ''    
        selectRange={true}
        minDetail="month" //   ''   
        maxDetail="month" //   ''   
        navigationLabel={null}
        showNeighboringMonth={false} //  ,      
        prev2Label={null}
        next2Label={null}
        // className="mx-auto w-full text-sm border-b"
        activeStartDate={null}
      />
      {selectedDates.length === 2 ? (
        <div className="my-3 text-center">
          <p>
            {moment(selectedDates[0]).format("YYYY-MM-DD")} ~{" "}
            {moment(selectedDates[1]).format("YYYY-MM-DD")}
          </p>
          {dayDiff()}
        </div>
      ) : (
        <div className="my-3 text-center"> !</div>
      )}
    </div>
  );
}

export default TravelCalendar;
