import React, { useState } from "react";
import { Navbar, Nav, Button, Container } from "react-bootstrap";
import RegistModal from "../modals/RegistModal";
import LoginModal from "../modals/LoginModal";
import Dropdown from "../components/Dropdown";
import { useSelector } from "react-redux";

const Header = () => {
  // axios  username   
  // ( )      (navbar )
  // (  )    

  const [RegistModalOn, setRegistModalOn] = useState(false);
  const [LoginModalOn, setLoginModalOn] = useState(false);
  const username = useSelector((state) => state.UserNameReducer.username);

  // const token = Cookies.get("accessToken");
  // const username = Cookies.get("username");

  if (username !== "") {
    console.log("have", username);
    return (
      <>
        <header>
          <Navbar bg="light" expand="lg" id="radius">
            <Container style={{ paddingLeft: "3%", paddingRight: "5%" }}>
              <Navbar.Brand href="/">
                <img
                  src="images\logo.png"
                  alt="For. travel"
                  width="auto"
                  height="40vh"
                />
              </Navbar.Brand>
              <Dropdown />
            </Container>
          </Navbar>
        </header>
      </>
    );
  } else {
    console.log("no have");

    return (
      <>
        <RegistModal
          show={RegistModalOn}
          onHide={() => setRegistModalOn(false)}
        />
        <LoginModal show={LoginModalOn} onHide={() => setLoginModalOn(false)} />
        <header>
          <Navbar bg="light" expand="lg" id="radius">
            <Container style={{ paddingLeft: "3%", paddingRight: "5%" }}>
              <Navbar.Brand href="/">
                <img
                  src="images\logo.png"
                  alt="For. travel"
                  width="auto"
                  height="40vh"
                />
              </Navbar.Brand>

              <Navbar.Toggle aria-controls="basic-navbar-nav" />
              <Navbar.Collapse
                id="basic-navbar-nav"
                className="justify-content-end"
              >
                <Nav className="ml-auto">
                  <Nav.Link>
                    <Button
                      variant="primary"
                      onClick={() => setLoginModalOn(true)}
                      id="fullBtn"
                    >
                      Login
                    </Button>
                  </Nav.Link>
                  <Nav.Link>
                    <Button
                      variant="secondary"
                      onClick={() => setRegistModalOn(true)}
                      id="fullBtn"
                    >
                      Sign Up
                    </Button>
                  </Nav.Link>
                </Nav>
              </Navbar.Collapse>
            </Container>
          </Navbar>
        </header>
      </>
    );
  }
};
// };

export default Header;
