import React from "react";
import { Form, Button, Container } from "react-bootstrap";
import { useState } from "react";
import axios from "axios";
import { useDispatch } from "react-redux";
import { selectupdate } from "../Reducer/UpdateUserReducer";
import { setUsername } from "../Reducer/UserNameReducer";

const UpdateUsernameContent = () => {
  const [newUsername, setNewUsername] = useState("");
  const [password, setPassword] = useState("");

  const dispatch = useDispatch();
  const onChangeUsername = (e) => {
    setNewUsername(e.target.value);
  };
  const onChangePassword = (e) => {
    setPassword(e.target.value);
  };

  const submitUsername = () => {
    console.log(password, newUsername);
    if (newUsername && password)
      axios({
        url: "http://localhost:3000/update/user",
        method: "post",
        withCredentials: true,
        data: {
          newUsername: newUsername,
          password: password,
        },
      })
        .then((res) => {
          console.log("res.data", res.data);
          if (res.status === 200) {
            alert("username !");
            dispatch(setUsername(res.data.username));
            window.location.reload();
          }
        })
        .catch((err) => {
          alert(`${err.response.data.message}\n .`);
          console.log(err);
        });
    else if (newUsername === "" && password)
      alert("  .");
    else if (newUsername && password === "") alert(" .");
    else if (newUsername === "" && password === "")
      alert("  .");

    setNewUsername("");
    setPassword("");
  };
  const activeEnter = (e) => {
    if (e.key === "Enter") submitUsername();
  };
  return (
    <Container style={{ marginTop: "3%" }}>
      <Form id="Form" className="UpdateUserNameBox">
        <div style={{ display: "flex", justifyContent: "end" }}>
          <Button
            id="exitBtn"
            variant="danger"
            onClick={() => {
              dispatch(selectupdate());
            }}
          >
            x
          </Button>
        </div>
        <Form.Group className="updateForm">
          <Form.Label>
            <h1 className="my-4">Username </h1>
          </Form.Label>
        </Form.Group>
        <Form.Group className="updateForm">
          <Form.Label>Username </Form.Label>
          <Form.Control
            id="radius"
            type="text"
            value={newUsername}
            onChange={onChangeUsername}
            placeholder="Username"
            className="my-2"
          />
        </Form.Group>
        <Form.Group className="updateForm">
          <Form.Label>Password </Form.Label>
          <Form.Control
            id="radius"
            type="password"
            value={password}
            onChange={onChangePassword}
            onKeyDown={(e) => {
              activeEnter(e);
            }}
            placeholder="Password"
            className="my-2"
          />
        </Form.Group>
        <Form.Group className="updateForm">
          <Button
            id="radius"
            className="fullBtn"
            variant="info"
            onClick={() => {
              submitUsername();
            }}
          >
            Username 
          </Button>
        </Form.Group>
      </Form>
    </Container>
  );
};

export default UpdateUsernameContent;
